import React, { useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';
import './home.scss';

export default props => {
  const { setPath } = props;
  const isTabletorMobile = useMediaQuery({ maxWidth: 767.98 });
  const history = useHistory();

  useEffect(() => setPath('/'), [])

  const navPush = nav => {
    setPath(nav);
    history.push(nav);
  };

  return <main className='home-container'>
    <article className={'icon-container' + (isTabletorMobile ? ' flex-col' : ' flex-row')}>
      <div className='flex-col'>
        <img onClick={() => navPush('/widkt')} src='icon/Atom-icon.png' alt='Art of hand' />
        <h3>Provocative Programming</h3>
        <p> Almost forty first world nations are spending billions on fusion power research at ITER. What would you say if you learned that a form of fusion power technology exists today and is being ignored by both politicians and scientists simply because they’ve tied their wagon to the ITER horse? Want to know more? We will take a very close look at this controversial technology in a major <em>&ldquo;Wow! I Didn’t Know That!&rdquo;</em> story.
        </p>
      </div>
      <div className='flex-col'>
        <img onClick={() => navPush('/dunerider')} src='icon/Electric-plug-icon.png' alt='Art of electric' />
        <h3>&ldquo;Gas Becomes Electra&rdquo;</h3>
        <p>The era of the gasoline-fueled automobile nears its end. <em>&ldquo;Dune Rider!&rdquo;</em> takes the public television audience for a wild ride on a beyond-the-state-of-the-art experimental on- and off-road vehicle embodying the best electric propulsion technology known today.
        </p>
      </div>
      <div className='flex-col'>
        <img onClick={() => navPush('/aop')} src='icon/Programming.png' alt='Art of programming console' />
        <h3>Programming Provocatively</h3>
        <p>Our television + Internet educational series, <em>&ldquo;The Art of Programming&rdquo;</em>, features an effective and surprisingly straightforward method of realizing the long-sought potential of computer-based training. The difference between a computer and a real teacher – a live, responsive, fully interactive, human person! – has been that you can’t ask the computer a question. We have the solution.
        </p>
      </div>
    </article>

    <div className='cyan-back'>
      <article className={'radio-container' + (isTabletorMobile ? ' flex-col' : '')}>
        <h2>An Innovative Public Radio Series</h2>
        <section className='txt-img-wrapper'>
          <div>
            <h3>Introduction to <span className='italic'>Wow! I Didn't Know That! (WIDKT!)</span></h3>
            <p>
              Our series <span className='italic'>WIDKT!</span> will select an unusual and intriguing topic from one of many branches in science, capture the heart of the story, and tell it in a way that listeners will easily understand. Within the format of half an hour, each story will be developed in fine detail while also being made appealing. An increase in budget would allow for the format to possibly be doubled to a full hour where there will be substantially greater involvement in each story told, which means more research, writing, and editing costs.
            </p>
            <div className='break'></div>
            <p>
              These stories will not come from Wikipedia or any other media sources on the Internet. They will not be repackaged press releases from high-tech manufacturers or national laboratories. Rather, these stories will be written and, in some instances, told by real scientists and engineers working with the science and technology described.
            </p>
            <center><button onClick={() => navPush('/widkt')}>Learn More</button></center>
          </div>
          <div className='img-container'>
            <img src='img/ExperiencedScientists.jpg' alt='Experienced scientists' />
          </div>
        </section>
      </article>
    </div>

    <div>
      <article className={'radio-container' + (isTabletorMobile ? ' flex-col' : '')}>
        <div className='break'></div>
        <div className='img-container'>
          <img src='img/2015_Ariel_Nomad_offroad_atv_dunebuggy_military_2560x1600.jpg' alt='Picture of off-road vehicle' />
        </div>
        <h2>Zero Emissions and a Wealth of Fun!</h2>
        <p>
        The <em>&ldquo;Dune Rider!&rdquo;</em> thirty-nine episode public television series presents the design, construction, and testing of a fascinating all-electric on- and off-road automobile built with aerospace materials and technologies. Its purpose is to encourage young people across the entire nation – <em>particularly women</em> – to consider careers in engineering. It will also enable audiences nationwide to better understand the promise of zero-emission vehicles (ZEV). It will make a clear and positive statement about the future of the nation’s automotive industry.
        </p>
        <div className='break'></div>
        <p>
        We will demonstrate the satisfaction that one derives from working constructively in engineering and technology, providing young viewers a reason to see higher education as the best route to success in life.
        If the <em>“Dune Rider!”</em> series encourages even a small number of people of high school age to consider and pursue an education in engineering, then it will have accomplished something worthwhile.
        </p>
        <div className='break'></div>
        <center><button onClick={() => navPush('/dunerider')}>Learn More</button></center>
      </article>
    </div>

    <article className='banner-container'>
      <div className='page-banner'>
        <img src='img/earth.jpg' />
        <div className='banner-text'>
          <h1>&ldquo;Education is the most important single function of civilization.&rdquo;</h1>
        </div>
      </div>
    </article>
    <article className='coding-container'>
      <h2>Our Vision for Teaching Software Development</h2>
      <section>
        <h3>Learning with <span className='italic'>&ldquo;The Art of Programming&rdquo;</span></h3>
        <p>
          For those who do not or simply cannot devote four years to a Computer Science course at university, we offer a sophisticated and extremely effective way of education via presentation by a skilled actor depciting an experience teacher in the classroom. Using a unique combination of proven teaching techniques and proprietary methods, we have developed a program that takes a "cookbook" approach to teaching coding. Our goal with <span className='italic'>The Art of Programming</span> is to present modern, high-quality educational material in the manner of the best of public television — engaging, expressive, and enthusiastic.
        </p>
      </section>
      <section className='txt-img-wrapper'>
        <div>
          <h3>An Emphasis on How the Viewer Actually Learns</h3>
          <p>
            Unlike hardcopy books which need multiple editions to make updates and corrections, our video series will be completely current, up-to-date, and consistent with the latest releases of the programming language and the tools used in its application to real and interesting problems. The examples used in explaining the principles will take the form of reusable modules that do useful tasks and which can be "plugged in" to the programs that the viewer creates on their own. Another important point is that the best way of learning is from a really good teacher, in the classroom, one-on-one. How can you possibly achieve this on the Internet? We've found a way that comes very close. It's not perfect, but it does work.
          </p>
          <center><button onClick={() => navPush('/aop')}>Learn More</button></center>
        </div>
        <div className='img-container'>
          <img src='img/StudyingWithOnlineVideoLesson.jpg' alt='Filming a man giving a lecture' />
        </div>
      </section>
    </article>
    <div className='cyan-back'>
      <article className='closing-container'>
        <div className={'wrapper' + (isTabletorMobile ? ' flex-col' : ' flex-row')}>
          <div className={isTabletorMobile ? '' : 'col-2'}>
            <h3>
              We are passionate about education being the most important function of civilization. However, education has yet to fully exploit the advantages offered by the Internet. With the Internet&rsquo;s ability to reach almost everyone, at any time and in any place, it opens doors to opportunities that people previously only dreamt of.
            </h3>
          </div>
          <div className={isTabletorMobile ? '' : 'col-2'}>
            <h3>
              Our mission is to continue creating high-quality Internet instruction by using our innovative, reliable, and cost-efficient techniques. Our management team has experience in getting the most out of limited resources. We never cut corners; only inefficiencies.
            </h3>
          </div>
        </div>
      </article>
    </div>
  </main>
}
