import React, { useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import './dunerider.scss';

export default props => {
  const { setPath } = props;
  const history = useHistory();

  useEffect(() => setPath('/dunerider'), [])

  const navPush = nav => {
    setPath(nav);
    history.push(nav)
  }

  return <main className='dunerider-container'>
    <h1>Test Thingie</h1>
  </main>
}
