import React, { useState, useEffect } from 'react';
import { useMediaQuery } from 'react-responsive';
import './contact.scss';

export default props => {
  const { setPath } = props;
  const isTabletorMobile = useMediaQuery({ maxWidth: 767.98 });
  const [name, setName] = useState('')
  const [email, setEmail] = useState('')
  const [subject, setSubject] = useState('')
  const [message, setMessage] = useState('')

  useEffect(() => setPath('/contact'), [])

  return <main className='contact-container'>
    <article className='info-container'>
      <h1>Talk to Us – We Listen!</h1>
      <section className={'wrapper' + (isTabletorMobile ? ' flex-col' : ' flex-row')}>
        <div className={(isTabletorMobile ? '' : 'col-3') + ' flex-col'}>
          <i className='fas fa-map-marker-alt' />
          <h3>Where We&rsquo;re At</h3>
          <p>1000 Rte 386 · Scottsville, New York 14546</p>
        </div>
        <div className={(isTabletorMobile ? '' : 'col-3') + ' flex-col'}>
          <i className='fas fa-mobile-alt' />
          <h3>We Return Phone Calls!</h3>
          <p>585·889·4026</p>
        </div>
        <div className={(isTabletorMobile ? '' : 'col-3') + ' flex-col'}>
          <i className='far fa-envelope' />
          <h3>E-mail</h3>
          <p>info@ep5.org</p>
        </div>
      </section>
    </article>
    <article className='form-container'>
      <h3>We&rsquo;d like to hear from you.</h3>
      <p>All messages will be acknowledged.</p>
      <form>
        <label>Name</label>
        <input value={name} onChange={e => setName(e.target.value)} />
        <label>E-mail</label>
        <input value={email} onChange={e => setEmail(e.target.value)} />
        <label>Subject</label>
        <input value={subject} onChange={e => setSubject(e.target.value)} />
        <label>Message</label>
        <textarea wrap='soft' rows='10' value={message} onChange={e => setMessage(e.target.value)} />
      </form>
      <center><button>Submit</button></center>
    </article>
  </main>
}
