import React, { useEffect } from 'react';
import './demos.scss';

export default props => {
  const { setPath } = props;

  useEffect(() => setPath('/demos'), [])

  return <main className='demos-container'>
    <article className='episode-container'>
      <h1>Demo Reels</h1>
      <section className='txt-img-wrapper'>
        <div>
          <h3>An Episode of <span className='italic'>&ldquo;WIDKT!&rdquo;</span></h3>
          <p>
            To provide a sense of what <em>&ldquo;Wow! I Didn't Know That!&rdquo;</em> will sound like, this demo reel is a condensed version of the first episode of the series. It consists of several features typical of what we&rsquo;ll present each week.
          </p>
          <div className='break'></div>
          <p>
            The goal was to combine modest levels of wit, whimsy, humor, and imagination with genuine scientific rigor to explain fascinating scientific ideas, questions, and news, all without dumbing anything down or talking down to the audience. The stories in this abbreviated episode merely touch on the variety of topics we&rsquo;ll examine.
          </p>
        </div>
        <div className='img-container'>
          <iframe src="https://www.youtube.com/embed/ZJoHNSZByO8?rel=0" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
        </div>
      </section>
    </article>
    <div className='cyan-back'>
      <article className='pottery-container'>
        <section className='txt-img-wrapper'>
          <div>
            <h3><span className='italic'>&ldquo;The Art of Pottery&rdquo;</span></h3>
            <p>
              Pottery and sculpture, using fired clays, may rival cave painting as mankind’s oldest art form. In 2011 and 2012, ep5 investigated the idea of a documentary on pottery. This grew into a proposed instructional series demonstrating the fundamentals of working with clay and firing it in kilns to form a variety of handmade <em>objets d’art</em>.
            </p>
            <div className='break'></div>
            <p>
              This video shows the screen tests in which two local potters auditioned for this proposed instructional (or “how-to”) series on the practical ceramic arts. Jim Kozlowski, a master of pottery and founder of the <a href="https://www.wizardofclay.com/" target="_blank">Wizard of Clay</a>, has been making pots, cups, vases, and other real-world and artistic works in clay for over half a century. Mitch Messina, a sculptor of note who exhibits nationally, is professor of art at a local college.
            </p>
            <div className='break'></div>
            <p>
              How well did they do on their screen tests?
            </p>
            <div className='break'></div>
            <p>
              Bear in mind that this was, as far as we know, Jim and Mitch’s first time in front of the camera, and they neither rehearsed nor read from scripts or prompters. Had the television series gone into production, both would have done very well.
            </p>
          </div>
          <div className='img-container'>
            <iframe src="https://www.youtube.com/embed/qlqeDeHGeUU?rel=0" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
          </div>
        </section>
      </article>
    </div>
  </main>
}
