import React, { useState } from 'react';
import { BrowserRouter, Route } from 'react-router-dom';
import Header from './components/Header';
import About from './components/About';
import Aop from './components/Aop';
import Contact from './components/Contact';
import Demos from './components/Demos';
import Dunerider from './components/Dunerider';
import Endorsements from './components/Endorsements';
import Home from './components/Home';
import Pdfs from './components/Pdfs';
import Widkt from './components/Widkt';
import Footer from './components/Footer';
import './App.scss';

export default () => {
  const [path, setPath] = useState('');

  return <div className='App'>
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <Header path={path} setPath={setPath} />
      <Route exact path='/' component={() => <Home setPath={setPath} />} />
      <Route exact path='/about' component={() => <About setPath={setPath} />} />
      <Route exact path='/aop' component={() => <Aop setPath={setPath} />} />
      <Route exact path='/contact' component={() => <Contact setPath={setPath} />} />
      <Route exact path='/demos' component={() => <Demos setPath={setPath} />} />
      <Route exact path='/dunerider' component={() => <Dunerider setPath={setPath} />} />
      <Route exact path='/endorsements' component={() => <Endorsements setPath={setPath} />} />
      <Route exact path='/pdfs' component={() => <Pdfs setPath={setPath} />} />
      <Route exact path='/widkt' component={() => <Widkt setPath={setPath} />} />
      <Footer setPath={setPath} />
    </BrowserRouter>
  </div>
}
