import React, { useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import './widkt.scss';

export default props => {
  const { setPath } = props;
  const history = useHistory();

  useEffect(() => setPath('/widkt'), [])

  const navPush = nav => {
    setPath(nav);
    history.push(nav)
  }

  return <main className='widkt-container'>
    <article className='intro-container'>
      <h1 className='italic'>&ldquo;Wow! I Didn't Know That!&rdquo;</h1>
      <div className='txt-img-wrapper'>
        <div>
          <h3>Science Stories for Grownups</h3>
          <p>
            What is <em>&ldquo;WIDKT!&rdquo;</em>? It&rsquo;s an informative, educational, and entertaining discussion of topics in science and technology, with wit, whimsy, and an occasional flavoring of controversy, all packaged in a novel format for public radio.
          </p>
          <div className='break'></div>
          <p>
            Did you ever have a “driveway moment” in your high school science class, when the bell rang and you wanted the teacher to go on with her explanation of something fascinating? No? Neither did we. Driveway moments, as NPR will earnestly assure you, occur only on public radio. Well, have we got a deal for you!
          </p>
          <div className='break'></div>
          <p>
            Currently in the development stage, <em>&ldquo;Wow! I Didn't Know That!&rdquo;</em> is an intellectually provocative science program both whimsical and serious that won't insult your intelligence or speak down to you. Intended for weekly release, <em>“Wow! I Didn't Know That!”</em> will run an hour and feature short stories about what&rsquo;s new and interesting in science and technology. We’ll bet that you won’t have heard these stories anywhere else. In fact, if you take the bet and we lose, we’ll let you buy us lunch at the best restaurant in town!
          </p>
        </div>
        <div className='img-container'>
          <img src='img/WomanRecordingFromHome.jpg' alt='Woman recording on microphone from home' />
        </div>
      </div>
      <section>
        <div className='break'></div>
        <p>In addition to news from science, technology, and engineering, <em>&ldquo;WIDKT!&rdquo;</em> will feature thought-provoking editorials.</p>
        <div className='break'></div>
        <p>
          Television programs about the nature of Nature use exotic and ultra-costly graphics to illustrate the behavior of the Universe. Their sometimes vague and always scientifically over-simplified explanations of the underlying science give the viewer a sense of the principles but not a clear insight. Radio must paint its pictures in words. Thus, the radio script expresses its ideas with far greater clarity. This characterizes the writing of each episode of <em>“Wow! I Didn't Know That!”</em>
        </p>
        <div className='break'></div>
        <p>
          Although <span className='italic'>&ldquo;WIDKT!&rdquo;</span> is presented in a news-magazine format, it does not actually report science news. In fact, one of the stories occurs in 1600 CE. Thus, <span className='italic'>&ldquo;WIDKT!&rdquo;</span> is not time-sensitive, as news is by definition. The benefit of this for both underwriter and broadcaster is that it can be aired on any date and as often as the radio station needs.
        </p>
        <div className='break'></div>
        <p>
          By the way, what makes its format novel? It will be the only one-hour science program that the public radio station can broadcast in its entirety, as a stand-alone first half-hour, or as a stand-alone second half-hour. In essence, three programs for the price of one.
        </p>
      </section>
    </article>
    <div className='cyan-back'>
      <article className='topics-container'>
        <h2>We Already Have More Than 2,000 Candidate Story Topics</h2>
        <section>
          <img src='icon/TimeTravelIcon.png' alt='Art of hourglass depicting passage of time' />
          <h3>Why time travel will always be science fiction.</h3>
          <p>
            Time travel is a lovely premise for sci-fi, but there are many problems with the concept in the real world. Various hypotheses exist regarding time travel, but they are unlikely to overcome the restraints set by what is currently known about physics.
          </p>
        </section>
        <section>
          <img src='icon/BlackHoleIcon.png' alt='Art of black hole' />
          <h3>What will keep massive black holes at the centers of galaxies from eventually devouring everything?</h3>
          <p>
            Unfortunately, nothing. Gravity is impossible to stop, affecting whatever it encounters. It is merely a matter of when.
          </p>
        </section>
        <section>
          <img src='icon/MicrobesIcon.png' alt='Art of microbes' />
          <h3>This is, has been, and always will be the Age of Microbes.</h3>
          <p>
            Very persuasive arguments have been made that the simpler the lifeform, the easier it is for that lifeform to exist and thrive. It has the greatest tolerance to extreme environments, and is even able to survive substances meant to specifically kill them. No matter how much Lysol you spray, at least some microbes will always survive, just as they have since being the first living things on Earth.
          </p>
        </section>
        <section>
          <img src='icon/HumanVSBacteriaIcon.png' alt='Art of human on left and bateria on right' />
          <h3>The war between humans and bacteria is not going well. What is the current outlook for the future?</h3>
          <p>
            Humans and bacteria both evolve, but bacteria evolve some six orders of magnitude faster. Thus, when antibiotics are able to kill 99.9% of the bacteria present, the remaining 0.1% evolve and expand, eventually rendering the antibiotic ineffective. And inventing a new antibiotic is extraordinarily difficult — the last new one dates back to 1987.
          </p>
        </section>
        <section>
          <img src='icon/PhysicsIcon.png' alt={'Art of a Newton\'s cradle'} />
          <h3>Why is empty space actually nothing of the sort?</h3>
          <p>
            According to quantum mechanics, there exist uncountable numbers of virtual particles which pop into and immediately out of existence. These particles cannot be seen, measured, or controlled, yet they exist in supposedly empty space. And their presence has been proved with an amazingly simple device.
          </p>
        </section>
        <section>
          <img src='icon/PeopleIcon.png' alt='Art of three people/figures' />
          <h3><span className='italic'>WIDKT!</span> Demo Reel</h3>
          <p>
            The opening story of this condensed episode presents a spirited defense of wind turbines as an alleged menace to birds. Then, on a more serious level, we examine what Enrico Fermi asked about ET and why the answer might be very bad news for the sci-fi community. Next, why reliance on stealth technology may not be such a good idea, after all. This is followed by a lively explanation of that dreadful social embarrassment, borborygmi.
          </p>
        </section>
      </article>
    </div>
    <article className='insights-container'>
      <section>
        <h3>Production</h3>
        <p>
          We estimate that completion of production of the full series will require approximately eighteen to twenty months. This will provide sufficient time for fine-tuning and refining each episode. In order to ensure the very best and most consistent quality across the entire 52 episodes, we’ll complete production of the series before releasing it for broadcast. Production cost for the full-year series will be approximately <em>half</em> of current public radio averages for high-end science programs.
        </p>
      </section>
      <div className='wrapper'>
        <h3>Reviews</h3>
        <section>
          <p>
            &ldquo;There wasn't anything I didn't like. I thought it was very well done. It has just the right mix of whimsy and seriousness. I particularly liked the segment on teaching science and the Socratic method. And I liked all the different voices. It definitely has the public radio vibe to it.&rdquo;
          </p>
          <p className='test-name'>- Marie de Roos</p>
        </section>
        <section>
          <p>
            &ldquo;You all keep it interesting with your inflections. I generally focus much more consistently with the written word, but the way you made this holds my attention well. The humor helped, as well. Bonus points for making the physicist a &lsquo;she&rsquo;.&rdquo;
          </p>
          <p className='test-name'>- Susie LaBrie</p>
        </section>
        <section>
          <p>
            &ldquo;It is interesting. Diverse topics. Your voices are clear and articulate. I really like that you identity yourselves. A thought - to create more individuality, theme musical piece for each voice.&rdquo;
          </p>
          <p className='test-name'>- Helen Steh</p>
        </section>
      </div>
    </article>
    <article className='learn-more-container'>
      <section>
        <h3>Want to Learn More?</h3>
        <center><button onClick={() => navPush('/contact')}>Contact Us</button></center>
      </section>
    </article>
  </main>
}
