import React, { useEffect } from 'react';
import './about.scss';

export default props => {
  const { setPath } = props;

  useEffect(() => setPath('/about'), [])

  return <main className='about-container'>
    <article className='intro-container'>
      <h1>About ep5</h1>
      <section className='txt-img-wrapper'>
        <div>
          <h3>Who We Are</h3>
          <p>
            The ep5 Educational Broadcasting Foundation is a New York nonprofit charitable institution established to produce educational content for public radio and television. We are at present privately funded and rely upon volunteers for almost all functions. For several years, ep5 produced a public radio series entitled <em>&ldquo;Ninety Second Science&rdquo;</em>. We released episodes at the rate of one per weekday, until production funding ended a few years ago.
          </p>
        </div>
        <div className='img-container'>
          <img src='img/CollaborativeWorkInTVStudio.jpg'/>
        </div>
      </section>
    </article>
    <div className='cyan-back'>
      <article>
        <h2>Our Story</h2>
        <section>
          <div>
            <h3>What We Are</h3>
            <p>
              ep5 was incorporated in New York in 1988 under the state's Not-For-Profit Corporation Law. We are registered with the Attorney-General's Charities Bureau, ID 44-09-89. ep5 qualifies under Internal Revenue Code §501(c)(3) as tax-exempt, and we are authorized to issue tax-deductible receipts for donations.
            </p>
          </div>
          <div>
            <h3>What We Do</h3>
            <p>
              Our corporate purpose is simple. We see enormous importance in education and how people receive it. Not including college and university costs, America spends well over two thirds of a trillion dollars a year on education alone. We are attempting to improve and expand education through the media of radio and television, at no cost to those benefitting.
            </p>
            <div className='break'></div>
            <p>
              Public broadcasting should do more than merely entertain. That suffices for commercial radio and television, but educational broadcasting has the potential for more. This is why we exist. It&rsquo;s that simple.
            </p>
          </div>
        </section>
      </article>
    </div>
  </main>
}
